body {
  margin: 0;
  padding: 0
}

.App{
  width: 100%;
  height: 100%;
}

.head-bar{
  width: 100%;
  height: auto;
  margin: 0px;
  padding: 1px;
}

.head-top-bar-right-container{
  width: 100%;
  height: 2rem;
 position: relative;
}

.head-top-bar-right{
  width: 40%;
  position: absolute;
  right: 0px;
  top: 0px;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
}

.head-menu-bar{
  width: 100%;
  height: 4rem;
  background-color: #E87D0E;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 0px 20px 0px 20px;
  box-sizing: border-box;
}

.head-menu{
  width: 60%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.head-menu a{
  text-decoration: none;
  color: white;
  width: 5.5rem;
  height: 1.6rem;
  text-align: center;
  display:flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
}

.head-menu a:hover{
  background-color: white;
  color: #E87D0E;
}

.head-main{
  position:relative ;
  height: 45rem;
  width: 100%;
}

 .head-main > img{
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
}

.head-main-content{
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 5;
  left: 0px;
  bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.head-main-content-text{
  width: 50%;
  height: 39rem;
  padding: 10px 0 30px 30px;
  box-sizing: border-box;
  float: left;
  padding-left: 40px;
}

.head-main-content img{
  width: 50%;
  height: 70%;
}

.head-main-content-text p{
  
  font-family: poppins;
}

.head-main-content-text p:nth-child(1){
  font-size: 1rem;
  color: #E87D0E;
}

.head-main-content-text h2{
  font-weight: 700;
  font-size: 2.5rem;
}

.head-main-content-text p:nth-child(2){
  font-size: 1rem;
}
 
.btn{
  width: 9rem;
  height: 3.5rem;
  border-radius: 10px;
  background-color: #E87D0E ;
  color: white;
  display: flex;
  align-items:  center;
  justify-content: center;
}

.section h2{
  padding:  30px 0 0 0;
}
.section > h2{
  width: 60%;
  height: auto;
  margin: 20px auto;
  text-align: center;
}

.section-left-text{ 
  height: 9rem;
  width: 70%;
  text-align: left;
  margin: 10px 0 30px 0;
}

.section p{
  font-size: 16px;
}

.s1{
  padding: 0 0 0 10vw;
}

.section-banner > img {
  width: 700px;
  height: 400px;
}
